import React, { useState, useEffect } from 'react';
import { useParams } from 'react-router-dom';
import {
  Card
} from 'semantic-ui-react';
import User, { UserProps } from '../components/User'


function Users() {
  const email: undefined | string = useParams().email;
  const [users, setUsers] = useState<UserProps[]>([]);

  function getUsers(email?: string) {
    const url = "/api/v1/users" + ((email !== undefined) ? `?email=${email}` : "");
    fetch(url)
      .then((res) => res.json())
      .then((data) => {
          setUsers(data);
      });
  }

  useEffect(() => {
    getUsers(email);
  }, [email]);

  return (
    <Card.Group centered>
    {users && users.map((user: UserProps) => <User key={user.email} {...user} />)}
    </Card.Group>
  );
}

export default Users;
