import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import {
  Table,
  Pagination,
  PaginationProps,
  Menu,
  Dropdown,
  Statistic
} from 'semantic-ui-react';


type Record = {
  country: string;
  population: string;
  cured: number;
  deaths: number;
  discover_country: string;
  disease: string;
  pathogen: string;
  disease_code: string;
  email: string;
  first_enc_date: string;
  name: string;
  surname: string;
}

enum SortOrder {
  asc = "asc",
  desc = "desc"
}

type Stats = {
  countries: number;
  diseases: number;
  records: number;
  publicServants: number;
};

function capitalize(s: string) {
  return s.charAt(0).toUpperCase() + s.slice(1);
}


function Data() {
  // Records
  const [count, setCount] = useState(0);
  const [pageCount, setPageCount] = useState(0);
  const [records, setRecords] = useState([]);

  // Stats
  const [stats, setStats] = useState<Stats>({countries: 0, diseases: 0, records: 0, publicServants: 0});
  
  // Pagination
  const [currentPage, setCurrentPage] = useState<number>(1);
  const [number, setNumber] = useState<number>(10);

  // Sorting
  const [sortField, setSortField] = useState<string>("disease");
  const [sortOrder, setSortOrder] = useState<SortOrder>(SortOrder.asc);

  function getPage(page: number): void {
    fetch(`/api/v1/records?page=${page - 1}&number=${number}&sortField=${sortField}&sortOrder=${sortOrder}`)
      .then((res) => res.json())
      .then((data) => {
        console.log(data);
        const { count, records } = data;
        setRecords(records);
        setCount(count);
        setPageCount(Math.ceil(count / number));
      })
      .catch((err) => {
        console.error(err);
      });
  }

  useEffect(() => {
    getPage(currentPage);
  }, [currentPage, sortOrder, sortField, number]);

  useEffect(() => {
    fetch("/api/v1/stats")
      .then((res) => res.json())
      .then((data) => setStats(data));
  }, []);


  return (
    <>
    <Statistic.Group widths="four">
      <Statistic>
        <Statistic.Value>{stats.records}</Statistic.Value>
        <Statistic.Label>Records</Statistic.Label>
      </Statistic>
      <Statistic>
        <Statistic.Value>{stats.countries}</Statistic.Value>
        <Statistic.Label>Countries</Statistic.Label>
      </Statistic>
      <Statistic>
        <Statistic.Value>{stats.diseases}</Statistic.Value>
        <Statistic.Label>Diseases</Statistic.Label>
      </Statistic>
      <Statistic>
        <Statistic.Value>{stats.publicServants}</Statistic.Value>
        <Statistic.Label>Public Servants</Statistic.Label>
      </Statistic>
    </Statistic.Group>
    <Table sortable celled>
    <Table.Header>
      <Table.Row>
        <Table.HeaderCell>â„–</Table.HeaderCell>
        <Table.HeaderCell>Country</Table.HeaderCell>
        <Table.HeaderCell>Population</Table.HeaderCell>
        <Table.HeaderCell>Disease</Table.HeaderCell>
        <Table.HeaderCell>Pathogen</Table.HeaderCell>
        <Table.HeaderCell>Deaths</Table.HeaderCell>
        <Table.HeaderCell>Cured</Table.HeaderCell>
        <Table.HeaderCell colSpan='2'>Disease discovered in</Table.HeaderCell>
        <Table.HeaderCell>Author</Table.HeaderCell>
      </Table.Row>
    </Table.Header>

    <Table.Body>
      {records && records.map((record: Record, index: number) => (
        <Table.Row key={index}>
          <Table.Cell>{(currentPage - 1) * number + index + 1}</Table.Cell>
          <Table.Cell>{record.country}</Table.Cell>
          <Table.Cell>{record.population}</Table.Cell>
          <Table.Cell>{record.disease.toUpperCase()}</Table.Cell>
          <Table.Cell>{capitalize(record.pathogen)}</Table.Cell>
          <Table.Cell>{record.deaths}</Table.Cell>
          <Table.Cell>{record.cured}</Table.Cell>
          <Table.Cell>{record.discover_country}</Table.Cell>
          <Table.Cell>{new Date(record.first_enc_date).toLocaleDateString()}</Table.Cell>
          <Table.Cell><Link to={`/users/${record.email}`}>{`${record.name} ${record.surname}`}</Link></Table.Cell>
        </Table.Row>
      ))}
    </Table.Body>

    <Table.Footer>
      <Table.Row>
        <Table.HeaderCell colSpan='1'>
          {count}
        </Table.HeaderCell>

        <Table.HeaderCell colSpan='3'>
          <Menu compact horizontal>
            <Dropdown item text='Sort by'>
              <Dropdown.Menu>
                <Dropdown.Item onClick={() => {
                  setSortField("country");
                  setCurrentPage(1);
                }}>
                  Country
                </Dropdown.Item>
                <Dropdown.Item onClick={() => {
                  setSortField("population");
                  setCurrentPage(1);
                }}>
                  Population
                </Dropdown.Item>
                <Dropdown.Item onClick={() => {
                  setSortField("disease");
                  setCurrentPage(1);
                }}>
                  Disease
                </Dropdown.Item>
                <Dropdown.Item onClick={() => {
                  setSortField("pathogen");
                  setCurrentPage(1);
                }}>
                  Pathogen
                </Dropdown.Item>
                <Dropdown.Item onClick={() => {
                  setSortField("deaths");
                  setCurrentPage(1);
                }}>
                  Deaths
                </Dropdown.Item>
                <Dropdown.Item onClick={() => {
                  setSortField("cured");
                  setCurrentPage(1);
                }}>
                  Cured
                </Dropdown.Item>
                <Dropdown.Item onClick={() => {
                  setSortField("discover_country");
                  setCurrentPage(1);
                }}>
                  Discovery country
                </Dropdown.Item>
                <Dropdown.Item onClick={() => {
                  setSortField("first_enc_date");
                  setCurrentPage(1);
                }}>
                  Discovery date
                </Dropdown.Item>
              </Dropdown.Menu>
            </Dropdown>
            <Dropdown item text='Order'>
              <Dropdown.Menu>
                <Dropdown.Item onClick={() => {
                  setSortOrder(SortOrder.asc);
                  setCurrentPage(1);
                }}>
                  Ascending
                </Dropdown.Item>
                <Dropdown.Item onClick={() => {
                  setSortOrder(SortOrder.desc);
                  setCurrentPage(1);
                }}>
                  Descending
                </Dropdown.Item>
              </Dropdown.Menu>
            </Dropdown>
            <Dropdown item text='Results'>
              <Dropdown.Menu>
                <Dropdown.Item onClick={() => {
                  setNumber(5);
                  setCurrentPage(1);
                }}>
                  5
                </Dropdown.Item>
                <Dropdown.Item onClick={() => {
                  setNumber(10);
                  setCurrentPage(1);
                }}>
                  10
                </Dropdown.Item>
                <Dropdown.Item onClick={() => {
                  setNumber(20);
                  setCurrentPage(1);
                }}>
                  20
                </Dropdown.Item>
                <Dropdown.Item onClick={() => {
                  setNumber(50);
                  setCurrentPage(1);
                }}>
                  50
                </Dropdown.Item>
              </Dropdown.Menu>
            </Dropdown>
          </Menu>
        </Table.HeaderCell>
        
        <Table.HeaderCell colSpan='7'>
         <Pagination
            activePage={currentPage}
            boundaryRange={1}
            onPageChange={(_: any, data: PaginationProps) => {
              const { activePage } = data;
              if (typeof(activePage) === "number") {
                setCurrentPage(activePage);
              }
            }}
            siblingRange={1}
            totalPages={pageCount}
          /> 
        </Table.HeaderCell>
      </Table.Row>
    </Table.Footer>
  </Table>
  </>
  );
}

export default Data;
